<template>
  <div ref="resizeRef" id="container">
    <svg ref="svgRef">
    </svg>
  </div>
</template>

<script>
import { onMounted, ref } from "vue";
import {
  select
} from "d3";

import useResizeObserver from "@/use/resizeObserver";

const drawRect = (height,index,svg) =>{
        let rect = svg.append("rect")  //添加一个矩形
            .attr("x",index)
            .attr("y",-1 * height)
            .attr("width",100)
            .attr("height",height)
            .attr("fill","red")
            .lower()
        
        var rectTran = rect.transition()
                            // .delay(500)
                            .duration(3000)
                            // .ease("bounce")
                            .attr("y",1000);
        rectTran.remove();
        // console.log(rect,props,resizeState,rectTran)

}

export default {
  name: "RectAm",
  props: ["data"],
  setup(props) {
    const svgRef = ref(null);
    const { resizeRef, resizeState } = useResizeObserver();
    
    onMounted(() => {
        const svg = select(svgRef.value);
        drawRect(200,1,svg)
        svg.append("rect")  //添加一个矩形
                .attr("x",0)
                .attr("y",-200)
                .attr("width",800)
                .attr("height",200)
                .attr("fill","#eee")
        console.log(props,resizeState)
    });

    return { svgRef, resizeRef };
  },

};
</script>
<style>
#container {
  height: 1000px;
  z-index: 1;
}
</style>
